import api from '../cloud/interface.js'
import router from '../router'
import Vue
    from 'vue'

const User = {
    isInit: false,
    referer: '/server/status',
    token: null,
    init(success, error) {
        api.user.checkLogin().then(result => {
            if (result.errCode) {
                if (result.errCode === 'APP_NOT_INSTALL_YET') {
                    Vue.prototype.$loading.close()
                    router.push('/install')
                } else {
                    success && success(false);
                }
            } else {
                this.token = result.data
                this.isInit = true
                success && success(true);
            }
        }).catch(msg => {
            error && error(msg);
        }).finally(() => {

        })
    },
    reset() {
        this.token = null
    },
    logout(cb) {
        api.user.logout().then(() => {
            this.isInit = false
            this.token = null
            cb && cb()
        }).catch(error => {
            console.log(error)
        })
    }
}
export default User;