// 导入所有接口
import apis from '../cloud/interface'
import http
    from '../cloud/http'

const install = Vue => {
    if (install.installed)
        return;

    install.installed = true;
    /**
     * 判断是否是json
     * @param str
     * @returns {boolean}
     */
    Vue.prototype.$request = {
        get(url, data) {
            data = data || {}
            return http({
                url: url,
                method: 'get',
                timeout: data.timeout || 0,
                params: data
            })
        },
        post(url, data) {
            data = data || {}
            return http({
                url: url,
                method: 'post',
                timeout: data.timeout || 0,
                data
            })
        }
    }
    Object.defineProperties(Vue.prototype, {
        // 注意，此处挂载在 Vue 原型的 $api 对象上
        $api: {
            get() {
                return apis
            }
        }
    })
}

export default install