import md5 from 'js-md5';

export default {
    install: function (Vue) {
        Vue.mixin({
            created: function () {
                //console.log("组件开始加载")
            },
            methods: {
                test: function () {
                    //console.log("mixin test");
                }
            }
        })
        /**
         * 判断是否是json
         * @param str
         * @returns {boolean}
         */
        Vue.prototype.$isJson = function (str) {
            if (typeof str == 'string') {
                try {
                    let obj = JSON.parse(str);
                    if (typeof obj == 'object' && obj) {
                        return true;
                    } else {
                        return false;
                    }
                } catch (e) {
                    return false;
                }
            }
        }
        /**
         * md5加密
         * @type {exports}
         */
        Vue.prototype.$md5 = md5;
        /**
         * 深拷贝
         * @param origin
         * @returns {any}
         */
        Vue.prototype.$copy = function (origin) {
            return JSON.parse(JSON.stringify(origin));
        };
    }
}