import axios from '../../cloud/http'

//检查登录
export const checkLogin = () => {
    return axios({
        url: '/check',
        timeout: 3000,
        method: 'get',
    })
}
//安装
export const install = data => {
    return axios({
        url: '/install',
        timeout: 3000,
        method: 'post',
        data
    })
}
//登陆
export const login = data => {
    return axios({
        url: '/login',
        timeout: 3000,
        method: 'post',
        data
    })
}
//退出登录
export const logout = data => {
    return axios({
        url: '/logout',
        method: 'get',
        data
    })
}



