import statusConfig from "./data/status.json";
import dateUtil
    from "./utils/date";
import moment
    from "moment";

const filters = {
    byteFix: (length) => {
        if (length < 1024) {
            return length + ' byte'
        } else if (length < 1024 * 1024) {
            return (length / 1024).toFixed(2) + ' KB'
        } else {
            return (length / 1024 / 1024).toFixed(2) + ' MB'
        }
    },
    /**
     * 百分比计算
     * @param num1
     * @param num2
     * @returns {string}
     */
    percent: (num1, num2) => {
        if (num1 === num2) {
            return '0';
        }
        if (num1 === 0) {
            return (num2 * 100).toFixed(2);
        } else if (num2 === 0) {
            return (num1 * 100).toFixed(2);
        }
        let remainder = 0
        let percent = 0
        if (num2 > num1) {
            remainder = num2 - num1
        } else {
            remainder = num1 - num2
        }
        percent = remainder / num2
        return (percent * 100).toFixed(2)
    },
    //状态码翻译
    statusTransfer: (status, type) => {
        return statusConfig[type][status] || '--'
    },
    //匹配数组
    arrayFindId: (id, arr, outputKey) => {
        let output = '--'
        arr.forEach(item => {
            if (item.id === id) {
                output = item[outputKey]
            }
        })
        return output;
    },
    //人性化时间
    HumanizedTime: dateTimeStamp => {
        let minute = 1000 * 60
        let hour = minute * 60
        let day = hour * 24
        let month = day * 30
        if (!dateTimeStamp || parseInt(dateTimeStamp) === 0) {
            return '--'
        }
        let diffValue = 0
        let n = Number(dateTimeStamp);
        if (isNaN(n)) {
            let date = `${moment().format('Y')}-${dateTimeStamp}`
            dateTimeStamp = Math.ceil(moment(date).valueOf() / 1000)
        }
        diffValue = new Date().getTime() - dateTimeStamp * 1000;
        if (diffValue < 0) {
            //若日期不符则计算还要多久到达
            return dateUtil.secondToHour((dateTimeStamp * 1000 - new Date().getTime()) / 1000) + '后'
        }
        let monthC = diffValue / month;
        let hourC = diffValue / hour;
        let minC = diffValue / minute;
        //计算今天已经过去多少小时
        let rtime = new Date(dateTimeStamp * 1000);
        let now = new Date();
        let pastHour = parseInt(hourC);
        let hourPastToday = now.format('h');
        let hourPastWithoutToday = parseInt(hourC) - parseInt(hourPastToday);
        let result = ""

        if (pastHour <= hourPastToday) {
            if (minC < 1) {
                result = "刚刚";
            } else if (pastHour < 1) {
                result = parseInt(minC) + "分钟前";
            } else if (pastHour >= 1 && pastHour < 3) {
                result = pastHour + "小时前";
            } else {
                result = "今天" + rtime.format('hh:mm');
            }
        } else if (pastHour >= hourPastToday && hourPastWithoutToday < 24) {
            result = "昨天" + rtime.format('hh:mm');
        } else if (hourPastWithoutToday > 24 && hourPastWithoutToday <= 48) {
            result = "前天" + rtime.format('hh:mm');
        } else {
            if (monthC >= 2) {
                result = moment(rtime).format('YYYY-MM-DD hh:mm');
            } else {
                result = rtime.format('MM-dd hh:mm');
            }
        }
        return result;
    }
}
export default filters