import Vue
    from 'vue'
import Router
    from 'vue-router'

Vue.use(Router);
//不需要用户权限的路由表
const childrens = [
    {
        path: '/server/status',
        component: resolve => require(['./views/server/Manage.vue'], resolve),
        meta: {title: '服务器状态'}
    },
    {
        path: '/server/queue',
        component: resolve => require(['./views/server/Queue.vue'], resolve),
        meta: {title: 'Redis队列'}
    },
    {
        path: '/server/crontab',
        component: resolve => require(['./views/server/Crontab.vue'], resolve),
        meta: {title: '排程任务'}
    },
    {
        path: '/database/maintain',
        component: resolve => require(['./views/database/MaintainableTables.vue'], resolve),
        meta: {title: '数据维护'}
    },
    {
        path: '/server/document',
        component: resolve => require(['./views/server/Document.vue'], resolve),
        meta: {title: '文档'}
    },
    {
        path: '/amis/demo',
        component: resolve => require(['./views/amis/Demo.vue'], resolve),
        meta: {title: 'amis'}
    }
]
//定义一个函数来创建router
let createRouter = function () {
    return new Router({
        mode: 'hash',
        base: '/',
        routes: [
            {
                path: '*',
                component: () => import('./views/404.vue')
            },
            {
                path: '/',
                component: () => import('./views/Index.vue'),
                meta: {title: '仪表盘'},
                redirect: '/server/status',
                children: childrens
            },
            {
                path: '/document/ws',
                component: () => import('./views/document/Ws.vue'),
                meta: {title: 'Webservice接口文档'},
            },
            {
                path: '/document/rpc',
                component: () => import('./views/document/Rpc.vue'),
                meta: {title: 'RPC接口文档'},
            },
            {
                path: '/login',
                component: () => import('./views/Login.vue'),
                meta: {title: '登陆'},
            },
            {
                path: '/install',
                component: () => import('./views/Install.vue'),
                meta: {title: '应用安装'},
            },
        ]
    });
}

// 初始化路由的时候，只挂载不需要用户权限的路由表
const router = createRouter(childrens)
router.beforeEach((to, from, next) => {
    next()
})
export default router